import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EInvoiceComponent } from './e-invoice.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core'; 
import { ScrollingModule } from '@angular/cdk/scrolling';
import { QuickSendToCqtComponent } from './quick-send-to-cqt/quick-send-to-cqt.component';
import { EditCustomerTaxComponent } from './edit-customer-tax/edit-customer-tax.component';

const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: EInvoiceComponent
      }, 
      {
        path: "quick-send-to-cqt",
        component: QuickSendToCqtComponent
      }
    ]
  }
]
@NgModule({
  declarations: [
    EInvoiceComponent,
    QuickSendToCqtComponent,
    EditCustomerTaxComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ScrollingModule,
    ReactiveFormsModule,
  ]
})
export class EInvoiceModule { }
